.p-mv-swiper {
   width: 100%;
   height: 100%;
   .swiper-wrapper {

       .swiper-slide {
           overflow: hidden;

           img {
             
           }
       }
       
       //Prev
       .swiper-slide-prev,
       //Active
       .swiper-slide-active,
       //
       .swiper-slide-duplicate-active {

           img {
               animation-name: zoom;
               animation-duration: 8s;
               animation-fill-mode: forwards;
           }
       }
   }
}

